* {
  font-family: monospace;
  color: white;
}

@font-face {
  font-family: "MeteoconsRegular";
  src: url("../public/meteocons-webfont.eot");
  src: url("../public/meteocons-webfont.eot?#iefix") format("embedded-opentype"),
    url("../public/meteocons-webfont.woff") format("woff"),
    url("../public/meteocons-webfont.ttf") format("truetype"),
    url("../public/meteocons-webfont.svg#MeteoconsRegular") format("svg");
  font-weight: normal;
  font-style: normal;
}

#rio {
  color: gray;
}

[data-icon]:before {
  font-family: "MeteoconsRegular";
  content: attr(data-icon);
}

.icone-tempo {
  font-size: 270px;
}

.icone-local {
  font-size: 50px;
  margin-top: 13px;
  margin-right: 13px;
  color: gray;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100dvw;
  height: 100dvh;
  background-image: url("../public/gandalf.png");
  background-size: cover;
}

.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 27%;
  height: 80%;
}

.header {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  background-color: #f4efeb;
  height: 10.5%;
  color: gray;
  font-size: large;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 37%;
  background-color: #ffd21e82;
}

.img {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 60%;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  height: 100%;
}

.content {
  display: flex;
  justify-content: end;
  flex-direction: row;
  align-items: center;
  background-color: #facc05;
  height: 15%;
  font-size: 27px;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  height: 80%;
  font-size: 27px;
}

.content2 {
  display: flex;
  justify-content: end;
  flex-direction: row;
  align-items: center;
  background-color: #b89505;
  height: 15%;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  height: 80%;
}

img {
  width: 80%;
}

.start {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  height: 10%;
  font-size: 27px;
}

.middle {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  height: 8%;
  font-size: 28px;
}

.end {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  margin-top: 20px;
}
